.product-group {
  width: 100%;
  margin: 0.625rem auto;
  clear: both;
  background-color: #fff;
  padding-left: 0.625rem;
  padding-right: 0.625rem;
  overflow: hidden;
  position: relative;
}
.product-group .prev {
  width: 1.25rem;
  height: 3.75rem;
  position: absolute;
  left: 0;
  top: 20%;
  text-indent: -3.125rem;
  overflow: hidden;
  background-image: url('https://img.limeetpet.com/limeet/icon/page-pre_1.png');
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}
.product-group .prev:hover {
  background-image: url('https://img.limeetpet.com/limeet/icon/page-pre_3.png');
}
.product-group .prev.end {
  background-image: url('https://img.limeetpet.com/limeet/icon/page-pre_4.png');
}
.product-group .next {
  width: 1.25rem;
  height: 3.75rem;
  position: absolute;
  right: 0;
  top: 20%;
  text-indent: -3.125rem;
  overflow: hidden;
  background-image: url('https://img.limeetpet.com/limeet/icon/page-next_1.png');
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
}
.product-group .next:hover {
  background-image: url('https://img.limeetpet.com/limeet/icon/page-next_3.png');
}
.product-group .next.end {
  background-image: url('https://img.limeetpet.com/limeet/icon/page-next_4.png');
}
.product-group ul {
  padding: 0;
  width: 21.5625rem;
  clear: both;
  margin: 0 auto;
  min-height: 3.75rem;
}
.product-group ul li {
  width: 25%;
  float: left;
  margin-left: 0;
  background-color: #fff;
  margin-bottom: 0.46875rem;
  margin-top: 0.46875rem;
}
.product-group .item {
  width: 4.1875rem;
  margin: 0 auto;
}
.product-group .item .img-box {
  width: 4.1875rem;
  height: 4.1875rem;
}
.product-group .item .img-box img {
  max-width: 4.1875rem;
  max-height: 4.1875rem;
}
.product-group .item .title {
  width: 4.125rem;
  padding-top: 0.3125rem;
  padding-left: 0.3125rem;
  padding-right: 0.3125rem;
  font-size: 0.4375rem;
  line-height: 0.5625rem;
  font-weight: 200;
  overflow: hidden;
}
.product-group .item .price {
  padding-top: 0.3125rem;
  padding-bottom: 0.3125rem;
  padding-left: 0.3125rem;
  padding-right: 0.3125rem;
  line-height: 0.75rem;
}
.product-group .item .price i.unit {
  color: #EE4D2D;
  font-size: 0.375rem;
  vertical-align: bottom;
  padding-right: 0.15625rem;
}
.product-group .item .price span.value {
  color: #EE4D2D;
  font-size: 0.5rem;
  vertical-align: bottom;
}
.product-group .item .price span.original-value {
  color: #EE4D2D;
  font-size: 0.375rem;
  vertical-align: bottom;
  background-color: #FFF5E2;
  padding: 0.0625rem 0.15625rem;
  margin-left: 0.625rem;
}
